@import url(//fonts.googleapis.com/css?family=Spicy+Rice);
body {
  background-image: url(/static/media/background.1d49e9ec.jpg);
  background-size: cover;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Spicy Rice', cursive;
  display:flex;
  flex-direction: column;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.embedSite{
  margin-top: 5rem;
}
.header {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  align-items: center;
}

.header__title {
  color: #eeff00;
  font-size: 8vw;
  margin-left: 2.5rem;
}

.header__title .container {
  letter-spacing: 3px;
}

.header__search {
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  font-size: 6vw;
}

.header__search .links {
  display: flex;
  justify-content: flex-end;
}

.header__search .links a {
  text-decoration: none;
  margin: 2rem;
}

.search-form {
  display: flex;
  justify-content: flex-end;
  margin: 1rem;
}

.search-form__bar {
  display: flex;
  align-items: flex-end;
}

.search-form__bar input {
  border: 2px solid darkorange;
  width: 40vw;
  min-height: 50px;
  min-width: 250px;
  font-size: 3vh;
  border-radius: 50px;
  padding-left: 1.5rem;
  outline: none;
}

.search-form__bar button {
  background: darkorange;
  border: none;
  min-height: 56px;
  min-width: 100px;
  width: 18vw;
  padding: 0 0.5rem;
  font-weight: bold;
  color: white;
  font-size: 18px;
  border-radius: 50px;
  margin-left: 1rem;
}

.search-option {
  width: 40%;
  margin: auto;
}

@media screen and (max-width: 700px) {
  .search-form__bar {
    flex-direction: column;
    align-items: center;
  }
  .search-form button {
    margin-top: 1rem;
  }
}
/*# sourceMappingURL=Header.css.map */
.results-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #f5f4f4;
}

.result {
  padding-top: 1rem;
  margin: 1rem 2rem;
  border: 2px solid black;
  border-radius: 40px;
  background-color: lightgoldenrodyellow;
  width: 40vw;
  min-width: 310px;
  color: coral;
}

.result__title {
  margin: 0.4em 0;
  font-size: 2em;
}

.result__publisher {
  margin: 0.4em 0;
  color: #000000;
  font-size: 1.5rem;
}

.result__publisher span {
  margin-left: 1rem;
  font-size: 1em;
  color: blueviolet;
}

.result__social {
  margin: 0.4em 0;
}

.result__image {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 40px;
}

.result__button {
  background-color: red;
  width: 10vw;
  min-width: 100px;
  border-radius: 30px;
  padding: 0.5rem;
  margin: 1rem;
  border: white 2px solid;
  color: aliceblue;
  font-weight: bold;
}
/*# sourceMappingURL=ResultItem.css.map */
